<template>
  <div class="loginmain" :style="userToken?'height: 185px': 'height: 126px'">
    <div v-if="userToken">
      <div class="linfo">
        <div class="limg" @click="jumpPage('profile', userId)">
          <img :src="userProfile.profile.avatarUrl" />
        </div>
        <div class="lother">
          <div class="lname" @click="jumpPage('profile', userId)">
            <span>{{userProfile.profile.nickname}}</span>
          </div>
          <div class="llevel">
            <img src="../../../../assets/user/level_white.png" />
            <span>{{userProfile.level}}</span>
          </div>
          <div :class="signInCode === -2?'not btn':'btn'" @click="userSignIn">{{signIn}}</div>
        </div>
      </div>

      <div class="lcount">
        <div>
          <span>{{userProfile.profile.eventCount}}</span>
          <span>动态</span>
        </div>
        <div>
          <span>{{userProfile.profile.follows}}</span>
          <span>关注</span>
        </div>
        <div>
          <span>{{userProfile.profile.followeds}}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>

    <div v-else>
      <div class="desc">登录网易云音乐，可以享受无限收藏的乐趣，并且无限同步到手机</div>
      <div class="btn" @click="login">用户登录</div>
    </div>
  </div>
</template>
<script>
import { request } from "../../../../network/request";
export default {
  name: "Login",
  data() {
    return {
      userProfile: {},
      signIn: "签 到",
      signInCode: 0,
    };
  },
  computed: {
    userToken() {
      return this.$store.state.userToken;
    },
    userId() {
      return this.$store.state.userId;
    },
  },
  watch: {
    userId(newVal) {
      if (newVal) {
        request({
          url: `/user/detail?uid=${newVal}`,
        }).then((response) => {
          this.userProfile = response;
        });
      }
    },
  },
  created() {
    if (this.userId) {
      request({
        url: `/user/detail?uid=${this.userId}`,
      }).then((response) => {
        this.userProfile = response;
      });
    }
    this.getDailySignIn();
  },
  methods: {
    login() {
      if (this.userToken) {
      } else {
        this.$store.commit("changeValue", {
          type: "loginShow",
          value: true,
        });
      }
    },
    userSignIn() {
      this.getDailySignIn();
    },
    getDailySignIn() {
      return request({
        url: "/daily_signin",
      })
        .then((response) => {
          this.signInCode = response.code;
        })
        .catch((error) => {
          this.signInCode = -2;
          this.signIn = "已签到";
        });
    },
    jumpPage(path, id) {
      this.$router.push({
        name: path,
        params: {
          id: id,
        },
      });
    },
  },
};
</script>
<style scoped>
.lcount {
  display: flex;
  color: #666;
  margin-left: 0px;
}
.lcount > div {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  height: 40px;
  cursor: pointer;
  border-left: solid 1px #e4e4e4;
}
.lcount > div:hover {
  color: #0c73c2;
}
.lcount > div > span:first-of-type {
  font-size: 20px;
}
.lsignin:hover {
  background: linear-gradient(0deg, #3392ce, #5ea3e3);
}
.lsignin {
  width: 100px;
  height: 31px;
  line-height: 31px;
  background: linear-gradient(0deg, #1d6ebe, #449cdc);
  text-align: center;
  font-size: 12px;
  word-spacing: 0.2rem;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 12px;
}
.llevel > span {
  position: absolute;
  left: 26px;
}
.llevel {
  margin-top: 5px;
  color: #999;
  position: relative;
}
.lother {
  margin-left: 20px;
}
.lname:hover > span {
  border-bottom: solid 1px;
}
.lname {
  font-size: 14px;
  color: #333;
  font-weight: bold;
  cursor: pointer;
}
.limg {
  padding: 1px;
  border: solid 2px #dadada;
}
.limg > img {
  width: 80px;
  cursor: pointer;
}
.linfo {
  display: flex;
  margin: 20px;
}
.btn {
  height: 31px;
  width: 100px;
  background-color: #bc070c;
  background: linear-gradient(0deg, #bc070c, #ed212a);
  color: #fff;
  border-radius: 5px;
  line-height: 31px;
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
  cursor: pointer;
}
.btn:hover {
  background: linear-gradient(0deg, #d4171e, #f32f36);
}
.not {
  background: linear-gradient(0deg, #e7e7e7, #fff);
  cursor: default;
  color: #c3c3b0;
  border: solid 1px #ccc;
}
.not:hover {
  background: linear-gradient(0deg, #e7e7e7, #fff);
}
.loginmain {
  width: 250px;
  background: linear-gradient(0deg, #f3f3f3, #fff);
  border: 1px solid #d3d3d3;
  border-bottom: none;
  font-size: 12px;
}

.desc {
  padding: 16px 20px;
  line-height: 20px;
}
</style>